import React, { useEffect, useState } from "react";
import { Button, Col, Form, Row } from "react-bootstrap";
import ReactDatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import { BsCalendarFill } from "react-icons/bs";
import TimePicker from "react-time-picker";
import "react-time-picker/dist/TimePicker.css";
import { AiFillClockCircle } from "react-icons/ai";
import { FaRegEdit } from "react-icons/fa";
import moment from "moment";
import "moment/locale/en-gb";
import { useDispatch, useSelector } from "react-redux";
import {
  createAppointmentDetail,
  getAptDetail,
  updateAptDetail,
} from "../../../../../app/features/appointmentDetails/appointmentDetails.slice";
import { useForm } from "react-hook-form";

export default function AppointmentDetail({
  checkedApt,
  setInputFieldValues,
  inputFieldValues,
}) {
  const { register, handleSubmit, reset } = useForm();
  const slotDescription = JSON.parse(localStorage.getItem("slotDescription"));

  const [selectedDate, setSelectedDate] = useState(null);
  const [selectedTime, setSelectedTime] = useState(null);
  const [editing, setEditing] = useState(false);
  const [inputText, setInputText] = useState("");
  const [characterLimit] = useState(1000);
  const dispatch = useDispatch();
  const appointmentDetails = useSelector(
    (state) => state?.appointmentDetails?.createAppointmentDetails?.data
  );
  const getAptDetails = useSelector(
    (state) => state?.appointmentDetails?.getAptDetails
  );

  const handleInputChange = (fieldName) => (e) => {
    setInputFieldValues((prevState) => ({
      ...prevState,
      [fieldName]: e.target.value,
    }));
  };
  //

  const remainingChar = characterLimit - inputText.length;

  // const handleAptDetails = (event) => {
  //   const { name, value } = event.target;
  //   setAptData((prevData) => ({
  //     ...prevData,
  //     [name]: value,
  //   }));
  // };
  const handleChange = (event) => {
    setInputText(event.target.value);
  };
  const handleTimeChange = (time) => {
    setSelectedTime(time);
  };

  useEffect(() => {
    const finalData = {
      appointmentId: slotDescription?.appointmentId,
    };
    dispatch(getAptDetail(finalData));
  }, [dispatch, slotDescription?.appointmentId]);

  const onCreateSuccess = (data) => {
    const finalData = {
      appointmentId: slotDescription?.appointmentId,
    };
    dispatch(getAptDetail(finalData));
    setEditing(false);
    reset();
  };

  console.log("appointmentDetails", appointmentDetails)
  function createAppointementDetails(data) {
    const finalData = {
      appointmentDetailId: appointmentDetails?.appointmentDetailId
        ? appointmentDetails?.appointmentDetailId
        : getAptDetails?.data?.appointmentDetailId
        ? getAptDetails?.data?.appointmentDetailId
        : 0,
      appointmentId: slotDescription?.appointmentId,
      presentingComplaint: data.presentingComplaint
        ? data.presentingComplaint
        : appointmentDetails?.presentingComplaint
        ? appointmentDetails?.presentingComplaint
        : "",
        historyOfPresentingComplaint: data.presentingComplaint
        ? data.presentingComplaint
        : appointmentDetails?.presentingComplaint
        ? appointmentDetails?.presentingComplaint
        : "",
      pastMedicalHistory: data.pastMedicalHistory
        ? data.pastMedicalHistory
        : appointmentDetails?.pastMedicalHistory
        ? appointmentDetails?.pastMedicalHistory
        : "",
      medications: data.medications
        ? data.medications
        : appointmentDetails?.medications
        ? appointmentDetails?.medications
        : "",
      allergies: data.allergies
        ? data.allergies
        : appointmentDetails?.allergies
        ? appointmentDetails?.allergies
        : "",
      familyHistory: data.familyHistory
        ? data.familyHistory
        : appointmentDetails?.familyHistory
        ? appointmentDetails?.familyHistory
        : "",
      socialHistory: data.socialHistory
        ? data.socialHistory
        : appointmentDetails?.socialHistory
        ? appointmentDetails?.socialHistory
        : "",
      suspectedDiagnosis: data?.suspectedDiagnosis
        ? data?.suspectedDiagnosis
        : appointmentDetails?.suspectedDiagnosis
        ? appointmentDetails?.suspectedDiagnosis
        : "",
      recommendedActions: data?.recommendedActions
        ? data?.recommendedActions
        : appointmentDetails?.recommendedActions
        ? appointmentDetails?.recommendedActions
        : "",
      signOff: data?.signOff
        ? data?.signOff
        : appointmentDetails?.signOff
        ? appointmentDetails?.signOff
        : "",
      appointmentAttended: checkedApt === true ? true : false,
      failuretoattendnotes: data?.failuretoattendnotes
        ? data?.failuretoattendnotes
        : appointmentDetails?.failuretoattendnotes
        ? appointmentDetails?.failuretoattendnotes
        : "",
    };
    if (getAptDetails?.data) {
      dispatch(updateAptDetail({ finalData, onCreateSuccess }));
    } else {
      dispatch(createAppointmentDetail({ finalData, onCreateSuccess }));
    }
  }

  return (
    <>
      <span className="d-flex justify-content-end">
        {editing === false && getAptDetails?.data && (
          <Button
            className="edit-btn py-1 px-3 d-flex align-items-center"
            // onClick={handleEditClick}
            onClick={() => {
              setEditing(true);
            }}
          >
            <FaRegEdit />
            <p className="mb-0 ps-2">Edit</p>
          </Button>
        )}
      </span>
      <Form onSubmit={handleSubmit(createAppointementDetails)}>
        <Row>
          {checkedApt && !getAptDetails?.data?.failuretoattendnotes ? (
            <>
              <Col xs={6} className="pe-0">
                <p
                  className="mb-2"
                  style={{ fontSize: "18px", fontWeight: 500 }}
                >
                  Appointment Date
                </p>
                <span className="d-flex">
                  <ReactDatePicker
                    selected={
                      slotDescription &&
                      new Date(slotDescription.appointmentDate)
                    }
                    onChange={(date) => setSelectedDate(date)}
                    dateFormat="dd MMM yyyy"
                    placeholderText="23 Jan 2023"
                    className="custom-field-picker py-2 px-3 w-100 bg-transparent"
                    disabled={true}
                  />
                  <BsCalendarFill
                    className="custom-date-icon"
                    size={18}
                    style={{ top: "12px", right: "34px" }}
                  />
                </span>
              </Col>
              <Col xs={6} className="ps-0">
                <p
                  className="mb-2"
                  style={{ fontSize: "18px", fontWeight: 500 }}
                >
                  Appointment Time
                </p>
                <div className="time-picker">
                  <TimePicker
                    value={slotDescription?.appointmentStartTime}
                    onChange={handleTimeChange}
                    name="appointmentTime"
                    format="HH:mm"
                    clearIcon={null}
                    disabled={true}
                    disableClock={false}
                    clockIcon={
                      <AiFillClockCircle
                        style={{ color: "#999999" }}
                        size={16}
                      />
                    }
                    renderValue={(value) =>
                      moment(value, "HH:mm").locale("en-gb").format("h:mm A")
                    }
                    className="w-100 custom-field-picker py-2 px-3 bg-transparent"
                  />
                </div>
              </Col>

              <Col xs={12}>
                <Form.Group
                  className="mt-3"
                  style={{ fontSize: "18px", fontWeight: 500 }}
                  controlId="presentingComplaint"
                >
                  <Form.Label className="mb-0">
                    Presenting Complaint
                    <span style={{ color: "#FF3A3A" }} className="fw-bold">*</span>
                  </Form.Label>
                  <Form.Control
                    as="textarea"
                    rows={1}
                    placeholder="Tiredness"
                    className={`pt-2 ${editing && "bg-transparent"}`}
                    {...register("presentingComplaint")}
                    name="presentingComplaint"
                    defaultValue={
                      inputFieldValues?.presentingComplaint ||
                      "" ||
                      getAptDetails?.data?.presentingComplaint
                    }
                    onChange={handleInputChange("presentingComplaint")}
                    maxLength={250}
                    required
                    readOnly={
                      getAptDetails?.data?.presentingComplaint && editing
                        ? false
                        : getAptDetails?.data?.presentingComplaint
                        ? true
                        : false
                    }
                  />
                </Form.Group>
              </Col>
              {/* <Col xs={12}>
                <Form.Group
                  className="mt-3"
                  style={{ fontSize: "18px", fontWeight: 500 }}
                  controlId="Historyofpresentingcomplaint"
                >
                  <Form.Label className="mb-0">
                    History of presenting complaint
                    <span style={{ color: "#FF3A3A" }} className="fw-bold">*</span>
                  </Form.Label>
                  <Form.Control
                    as="textarea"
                    rows={1}
                    placeholder="Tell about history of present complaint & examination"
                    className={`pt-2 ${editing && "bg-transparent"}`}
                    {...register("historyOfPresentingComplaint")}
                    name="historyOfPresentingComplaint"
                    defaultValue={
                      inputFieldValues?.historyOfPresentingComplaint ||
                      "" ||
                      getAptDetails?.data?.historyOfPresentingComplaint
                    }
                    onChange={handleInputChange("historyOfPresentingComplaint")}
                    readOnly={
                      getAptDetails?.data?.historyOfPresentingComplaint &&
                      editing
                        ? false
                        : getAptDetails?.data?.historyOfPresentingComplaint
                        ? true
                        : false
                    }
                    maxLength={250}
                    required
                  />
                </Form.Group>
              </Col> */}
              <Col xs={12}>
                <Form.Group
                  className="mt-3"
                  style={{ fontSize: "18px", fontWeight: 500 }}
                  controlId="pastMedicalHistory"
                >
                  <Form.Label className="mb-0">
                    Past Medical History
                    <span style={{ color: "#FF3A3A" }} className="fw-bold">*</span>
                  </Form.Label>
                  <Form.Control
                    as="textarea"
                    rows={2}
                    placeholder="Tell about past medical history"
                    className={`pt-2 ${editing && "bg-transparent"}`}
                    {...register("pastMedicalHistory")}
                    name="pastMedicalHistory"
                    defaultValue={
                      inputFieldValues?.pastMedicalHistory ||
                      "" ||
                      getAptDetails?.data?.pastMedicalHistory
                    }
                    onChange={handleInputChange("pastMedicalHistory")}
                    readOnly={
                      getAptDetails?.data?.pastMedicalHistory && editing
                        ? false
                        : getAptDetails?.data?.pastMedicalHistory
                        ? true
                        : false
                    }
                    maxLength={250}
                    required
                  />
                </Form.Group>
              </Col>
              <Col xs={12}>
                <Form.Group
                  className="mt-3"
                  style={{ fontSize: "18px", fontWeight: 500 }}
                  controlId="medications"
                >
                  <Form.Label className="mb-0">
                    Medications
                    <span style={{ color: "#FF3A3A" }} className="fw-bold">*</span>
                  </Form.Label>
                  <Form.Control
                    as="textarea"
                    rows={2}
                    placeholder="Tell about past medical history"
                    className={`pt-2 ${editing && "bg-transparent"}`}
                    {...register("medications")}
                    name="medications"
                    defaultValue={
                      inputFieldValues?.medications ||
                      "" ||
                      getAptDetails?.data?.medications
                    }
                    onChange={handleInputChange("medications")}
                    readOnly={
                      getAptDetails?.data?.medications && editing
                        ? false
                        : getAptDetails?.data?.medications
                        ? true
                        : false
                    }
                    maxLength={250}
                    required
                  />
                </Form.Group>
              </Col>
              <Col xs={12}>
                <Form.Group
                  className="mt-3"
                  style={{ fontSize: "18px", fontWeight: 500 }}
                  controlId="allergies"
                >
                  <Form.Label className="mb-0">
                    Allergies
                    <span style={{ color: "#FF3A3A" }} className="fw-bold">*</span>
                  </Form.Label>
                  <Form.Control
                    as="textarea"
                    rows={2}
                    placeholder="Tell about allergy history"
                    className={`pt-2 ${editing && "bg-transparent"}`}
                    {...register("allergies")}
                    name="allergies"
                    defaultValue={
                      inputFieldValues?.allergies ||
                      "" ||
                      getAptDetails?.data?.allergies
                    }
                    onChange={handleInputChange("allergies")}
                    readOnly={
                      getAptDetails?.data?.allergies && editing
                        ? false
                        : getAptDetails?.data?.allergies
                        ? true
                        : false
                    }
                    maxLength={250}
                    required
                  />
                </Form.Group>
              </Col>
              <Col xs={12}>
                <Form.Group
                  className="mt-3"
                  style={{ fontSize: "18px", fontWeight: 500 }}
                  controlId="familyHistory"
                >
                  <Form.Label className="mb-0">
                    Family History
                    <span style={{ color: "#FF3A3A" }} className="fw-bold">*</span>
                  </Form.Label>
                  <Form.Control
                    as="textarea"
                    rows={2}
                    placeholder="Tell about family history"
                    className={`pt-2 ${editing && "bg-transparent"}`}
                    {...register("familyHistory")}
                    name="familyHistory"
                    defaultValue={
                      inputFieldValues?.familyHistory ||
                      "" ||
                      getAptDetails?.data?.familyHistory
                    }
                    onChange={handleInputChange("familyHistory")}
                    readOnly={
                      getAptDetails?.data?.familyHistory && editing
                        ? false
                        : getAptDetails?.data?.familyHistory
                        ? true
                        : false
                    }
                    maxLength={250}
                    required
                  />
                </Form.Group>
              </Col>
              <Col xs={12}>
                <Form.Group
                  className="mt-3"
                  style={{ fontSize: "18px", fontWeight: 500 }}
                  controlId="socialHistory"
                >
                  <Form.Label className="mb-0">
                    Social History
                    <span style={{ color: "#FF3A3A" }} className="fw-bold">*</span>
                  </Form.Label>
                  <Form.Control
                    as="textarea"
                    rows={2}
                    placeholder="Tell about social history"
                    className={`pt-2 ${editing && "bg-transparent"}`}
                    {...register("socialHistory")}
                    name="socialHistory"
                    defaultValue={
                      inputFieldValues?.socialHistory ||
                      "" ||
                      getAptDetails?.data?.socialHistory
                    }
                    onChange={handleInputChange("socialHistory")}
                    readOnly={
                      getAptDetails?.data?.socialHistory && editing
                        ? false
                        : getAptDetails?.data?.socialHistory
                        ? true
                        : false
                    }
                    maxLength={250}
                    required
                  />
                </Form.Group>
              </Col>
              <Col xs={12}>
                <Form.Group
                  className="mt-3"
                  style={{ fontSize: "18px", fontWeight: 500 }}
                  controlId="suspectedDiagnosis"
                >
                  <Form.Label className="mb-0">
                    Suspected Diagnosis
                    <span style={{ color: "#FF3A3A" }} className="fw-bold">*</span>
                  </Form.Label>
                  <Form.Control
                    as="textarea"
                    rows={2}
                    placeholder="Tell about suspected diagnosis"
                    className={`pt-2 ${editing && "bg-transparent"}`}
                    {...register("suspectedDiagnosis")}
                    name="suspectedDiagnosis"
                    defaultValue={
                      inputFieldValues?.suspectedDiagnosis ||
                      "" ||
                      getAptDetails?.data?.suspectedDiagnosis
                    }
                    onChange={handleInputChange("suspectedDiagnosis")}
                    readOnly={
                      getAptDetails?.data?.suspectedDiagnosis && editing
                        ? false
                        : getAptDetails?.data?.suspectedDiagnosis
                        ? true
                        : false
                    }
                    maxLength={250}
                    required
                  />
                </Form.Group>
              </Col>
              <Col xs={12}>
                <Form.Group
                  className="mt-3"
                  style={{ fontSize: "18px", fontWeight: 500 }}
                  controlId="recommendedActions"
                >
                  <Form.Label className="mb-0">
                    Recommended Actions
                    <span style={{ color: "#FF3A3A" }} className="fw-bold">*</span>
                  </Form.Label>
                  <Form.Control
                    as="textarea"
                    rows={2}
                    placeholder="Tell about some recommended actions"
                    className={`pt-2 ${editing && "bg-transparent"}`}
                    {...register("recommendedActions")}
                    name="recommendedActions"
                    defaultValue={
                      inputFieldValues?.recommendedActions ||
                      "" ||
                      getAptDetails?.data?.recommendedActions
                    }
                    onChange={handleInputChange("recommendedActions")}
                    readOnly={
                      getAptDetails?.data?.recommendedActions && editing
                        ? false
                        : getAptDetails?.data?.recommendedActions
                        ? true
                        : false
                    }
                    maxLength={250}
                    required
                  />
                </Form.Group>
              </Col>
              <Col xs={12}>
                <Form.Group
                  className="mt-3"
                  style={{ fontSize: "18px", fontWeight: 500 }}
                  controlId="signOff"
                >
                  <Form.Label className="mb-0">
                    Sign off
                    <span style={{ color: "#FF3A3A" }} className="fw-bold">*</span>
                  </Form.Label>
                  <Form.Control
                    as="textarea"
                    rows={2}
                    placeholder=""
                    className={`pt-2 ${editing && "bg-transparent"}`}
                    {...register("signOff")}
                    name="signOff"
                    defaultValue={
                      inputFieldValues?.signOff ||
                      "" ||
                      getAptDetails?.data?.signOff
                    }
                    onChange={handleInputChange("signOff")}
                    readOnly={
                      getAptDetails?.data?.signOff && editing
                        ? false
                        : getAptDetails?.data?.signOff
                        ? true
                        : false
                    }
                    maxLength={250}
                    required
                  />
                </Form.Group>
              </Col>
            </>
          ) : (
            <>
              <Col xs={12}>
                <Form.Group
                  style={{ fontSize: "18px", fontWeight: 500 }}
                  controlId="failuretoattendnotes"
                >
                  <Form.Label className="mb-0">
                    Failure to attend notes
                  </Form.Label>
                  <Form.Control
                    as="textarea"
                    rows={5}
                    placeholder="Add notes for failure to attend"
                    className={`pt-2 ${editing && "bg-transparent"}`}
                    {...register("failuretoattendnotes", {
                      required: true,
                    })}
                    // value={inputText}
                    onChange={handleChange}
                    isInvalid={inputText.length > characterLimit}
                    style={{ height: "200px", whiteSpace: "pre-wrap" }}
                    maxLength={250}
                    defaultValue={
                      getAptDetails?.data?.failuretoattendnotes ?? ""
                    }
                    readOnly={
                      getAptDetails?.data?.failuretoattendnotes && editing
                        ? false
                        : getAptDetails?.data?.failuretoattendnotes
                        ? true
                        : false
                    }
                  />
                  <span
                    className="mt-3 float-right"
                    bg={`${
                      inputText.length > characterLimit ? "danger" : "primary"
                    }`}
                  >
                    Remaining characters {remainingChar}
                  </span>
                </Form.Group>
              </Col>
            </>
          )}
          {(!getAptDetails?.data || editing === true) && (
            <Col xs={12}>
              <Button className="mt-4 start-apt-btn float-right" type="submit">
                Save
              </Button>
            </Col>
          )}
        </Row>
      </Form>
    </>
  );
}
